$main-yellow: #f4ff00;
$main-black: #030200;
$main-violet: #68009b;
$main-gray: #e5e5e5;
@font-face {
    font-family: 'menu font';
    src: url('../fonts/acme-regular.ttf');
}

@font-face {
    font-family: 'title font';
    src: url('../fonts/alpha_echo.ttf');
}

@mixin btn-style {
    border: 1px solid $main-gray;
    border-radius: 3px;
    line-height: 1.5vw;
    font-size: 0.9vw;
    cursor: pointer;
    font-family: "menu font";
    &:hover {
        background-color: $main-violet;
        color: $main-yellow;
    }
}

.test {
    height: 200vh;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.container {
    width: 95%;
    margin-left: auto;
}

.header {
    background-color: #000;
    ul {
        list-style-type: none;
    }
    a {
        text-decoration: none;
        color: $main-yellow;
    }
    h1 {
        color: $main-violet;
        background-color: $main-yellow;
        margin: 0;
        text-align: center;
        line-height: 3.1vw;
        font-size: 2.7vw;
        font-family: "title font";
    }
}

.header__inner {
    display: grid;
    grid-template-columns: 20% 30% 30% 13% 7%;
    grid-template-rows: 3vw;
    align-items: center;
}

.header__menu {
    display: grid;
    grid-template-columns: repeat(3, minmax(35px, 1fr));
    grid-template-rows: 3vw;
    text-transform: uppercase;
    font-size: 0.95vw;
    text-align: center;
    padding-left: 0;
    margin: 0;
    font-family: "menu font";
    >li {
        &:first-child {
            border-left: 2px solid $main-gray;
        }
        line-height: 3vw;
        border-right:2px solid $main-gray;
    }
}

.header__nav {
    height: 100%;
}

.header__submenu1,
.header__submenu2,
.header__submenu3 {
    display: none;
    background-color: #000;
    padding-left: 0;
}

.header__menu-li1:hover,
.header__menu-li2:hover,
.header__menu-li3:hover {
    background-color: $main-yellow;
    &>a {
        color: $main-black;
    }
}

.header__menu-li1:hover>.header__submenu1 {
    display: block;
}

.header__menu-li3:hover>.header__submenu3 {
    display: block;
}

.header__registation {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .header__sign-up {
        background-color: $main-black;
        color: $main-yellow;
        @include btn-style;
    }
    .header__create-account {
        background-color: $main-yellow;
        color: $main-black;
        @include btn-style;
    }
}

.header__upload {
    text-align: center;
    font-weight: 700;
    font-size: 1vw;
    line-height: 3.1vw;
    &:hover {
        background-color: $main-yellow;
        color: $main-black;
    }
}

