.aside {
    width: 5%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000000;
    height: 100vh;
    a {
        text-decoration: none;
        color: $main-yellow;
        width: 2.6vw;
        margin: 0 auto;
    }
}

.aside__menu {
    list-style-type: none;
    display: grid;
    grid-template-rows: repeat(10, minmax(4.1vw, 1fr));
    height: 100vh;
    padding-left: 0;
    margin-top: 0;
}

.aside__menu-item1 {
    &>a {
        display: block;
        height: 4vw;
        width: 4.5vw;
        background: url(../img/top10.svg) no-repeat right center;
    }
    .aside__submenu-1 {
        padding: 0;
        margin-right: -10vw;
        transform: translate(5vw, -23vw);
        transition: .4s;
        background-color: $main-black;
        text-align: center;
        text-transform: uppercase;
        font-size: 1vw;
        border: 1px solid $main-violet;
        border-radius: 0 15px 15px 0;
        li {
            padding: 0.4vw 0;
            border-top: 1px solid $main-violet;
        }
    }
    &:hover {
        background-color: $main-violet;
    }
    &:hover>.aside__submenu-1 {
        transition: .4s;
        transform: translate(5vw, -1.3vw);
    }
}

.aside__sumbenu1-item1 {
    border-radius: 0 15px 0 0;
}

.aside__sumbenu1-item1:hover,
.aside__sumbenu1-item2:hover,
.aside__sumbenu1-item3:hover,
